#home{
    /* border:1px solid red; */
    display:flex;
    justify-content:space-between;
    gap:15%;
    align-items: center;
    background-color: var(--color-bg-primary);
    padding:5% 9% 2% 9%;
}

.detail{
    /* border:1px solid black; */
   padding: 2%;
}
span{
    color: var(--color-bg);
    font-size: 2.6rem;
}

.detail p{
    color: var(--color-light);
    font-size: 1.2rem;
    line-height: 30px;
    text-align: justify;
}


.detail>button{
    /* border:1px solid black; */
    margin: 30px 0;
    background:var(--color-bg);
    padding: 0.7rem 3.5rem;
    border-radius: 10px; 
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap:10px
}
.detail>button>a{
    font-size: 1.4rem;
    font-weight:bold;
    color: white;
}

.d_icone{
   font-size: 1.7rem;
   color: white;
}


.pro__img{
    /* border:1px solid black; */
    background-color: var(--color-bg);
    height:45vh;
    width:45vh;
    overflow: hidden;
    border-radius:58% 42% 43% 57% / 29% 31% 69% 71%  ;
}
.pro__img>img{
    width:100%;
    height: 100%;
   
}


/* media screen */
@media screen  and (max-width:480px){
    #home{
        /* border:1px solid red; */
        display:flex;
        flex-direction: column-reverse;
        justify-content: center;
        align-items: center;
        row-gap: 3rem;
        background-color: var(--color-bg-primary);
        padding:5% 9% 2% 9%;
    }
   
    .detail>h2,span{
        text-align: center;
        font-size: 2rem;
    }
    div>div:nth-child(1)>p{
        text-align:justify;
        margin: 1rem 0;
    }
    
   .detail p{
        color: var(--color-light);
        font-size: 1rem;
        line-height: 25px;
        text-align: justify;
    }
    .detail>button{
        margin: auto;
        margin-top: 0.5rem;
        display: flex;
        gap:0.5rem;
        padding: 0.7rem 3.5rem;
    }
    .detail>button>a{
        font-size: 1rem;
     
    }
    .d_icone{
       font-size: 1.4rem;
    }
    
}

@media screen and (min-width:481px) and (max-width:768px){
     
    #home{
        /* border:1px solid red; */
        margin: auto;
        display:flex;
        flex-direction: column-reverse;
        row-gap: 4rem;
        background-color: var(--color-bg-primary);
        padding:5% 9% 2% 9%;
    }
    .detail>h2,span{
        text-align: center;
    }
    div>div:nth-child(1)>p{
        text-align:justify;
        margin: 1rem 0;
    }
    .detail p{
        color: var(--color-light);
        font-size: 1.1rem;
        line-height: 25px;
        text-align: justify;
    }
    .detail>button{
        margin: auto;
        margin-top: 0.5rem;
        display: flex;
        gap:0.5rem;
        padding: 0.7rem 3.5rem;

    }
    .detail>button>a{
        font-size: 1.3rem;
        font-weight: light;
     
    }
    .d_icone{
       font-size: 1.4rem;
    }

}


@media only screen and (min-width: 769px) and (max-width: 960px) {
    .detail>h2{
        font-size: 1.4rem;
    }
    span{
        font-size: 2.4rem;
    }
    .detail p{
        color: var(--color-light);
        line-height: 25px;
        font-size: 1rem;
    }
    
    .detail>button{
        display: flex;
        gap:0.5rem;
        padding: 0.7rem 3.5rem;

    }
    .detail>button>a{
        font-size: 1.3rem;
     
    }
    .d_icone{
       font-size: 1.4rem;
    }
}




