#skills{
    /* border:1px solid black; */
    background-color: var(--color-bg-primary);
    padding:5% 9% 4% 9%;
}
#skills>h1{
    text-align: center;
    color:var(--color-bg);
    font-size: 3rem;
    margin-bottom: 3rem;
}
.skill__container_main{
    /* border:1px solid black; */
    margin:auto;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap:3%;
    

}
/* for skill icone and container */
   .frontend{
    display: grid;
    padding: 1rem;
    grid-template-columns: repeat(2,1fr);
    justify-content: space-between;
    gap:1rem;
    
   }
   article{
    /* border:1px solid black; */
    display: flex;
    align-items: center;
    gap:0.2rem;
    padding: 0.5rem;
   }
   .skills__details>p{
    font-size: 1.2rem;
   }
/* ---------- */
.skills-card{
   background-color: var(--color-bg-primary);
   box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
}
.skills-card>h3{
    text-align: center;
    margin-top: 1.5rem;
    font-size: 1.4rem;
}
.skill__container_main>div{
    /* border:1px solid black; */
    height:auto;
    padding: 4%;
    border-radius: 30px;
}



/* media Query */

@media screen  and (max-width:480px){
    #skills{
        padding:5% 9% 2% 9%;
    }
    .skill__container_main{
        /* border:1px solid black; */
        display: grid;
        grid-template-columns: repeat(1,1fr);
        gap:3%;
        margin:auto;
    
    }
    .skills-card{
        width:100%
    }
    /* for skill icone and container */
   .frontend{
    display: grid;
    padding: 0.5rem;
    grid-template-columns: repeat(2,1fr);
    justify-content: space-between;
    gap:0.5rem;
    
   }
   article{
    /* border:1px solid black; */
    display: flex;
    align-items: center;
    gap:0.5rem;
    padding: 0.3rem;
   }
   .skills__details>p{
    font-size: 0.9rem;
   }
/* ---------- */
.skills-card{
   background-color: var(--color-bg-primary);
   box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
}
.skills-card>h3{
    text-align: center;
    margin-top: 1rem;
    font-size: 0.9rem;
}
.skill__container_main>div{
    /* border:1px solid black; */
    height:auto;
    padding: 2%;
    border-radius: 30px;
}
.skills-card-img{
    height:25px;
    width:25px
}
}

@media screen and (min-width:481px) and (max-width:768px){
    #skills{
        padding:5% 9% 2% 9%;
    }
    .skill__container_main{
        /* border:1px solid black; */
        display: grid;
        grid-template-columns: repeat(1,1fr);
        gap:5%;
        margin:auto;
    
    }
}



@media only screen and (min-width: 769px) and (max-width: 960px) {
    #skills{
        padding:5% 9% 2% 9%;
    }
    .skill__container_main{
            /* border:1px solid black; */
            display: grid;
            grid-template-columns: repeat(1,1fr);
            gap:5%;
            margin:auto;

        }
}

@media screen and (min-width: 960px) and (max-width:1350px){
    #skills{
        padding:5% 9% 2% 9%;
    }
    .skill__container_main{
        /* border:1px solid black; */
        display: grid;
        grid-template-columns: repeat(2,1fr);
        gap:5%;
        margin:auto;

    }
}